.filterDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
}

.filterInput {
  padding: 12px 24px;
  /* outline: none; */
  border: 1px solid #37cc57;
  border-radius: 4px;
  cursor: pointer;
  color: #050505;

  &:hover {
    box-shadow: 0 1px 5px 1px rgba(0, 0, 0, 0.1);
  }

  &:focus-within {
    outline: 1px solid rgba(212, 143, 143, 0.658);
    box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.1);
  }

  &::placeholder {
    overflow: hidden;
    color: #777;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.4;
  }
}

.filterLabel {
  color: #37cc57;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
}

.list {
  display: flex;
  flex-direction: column;
}

.customStyle {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
  color: #37cc57;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}
